import React, { useEffect, useState } from 'react';
import { TableComposable, Thead, Tr, Th, Tbody, Td } from '@patternfly/react-table';
import {
  Button,
  OverflowMenu,
  OverflowMenuContent,
  OverflowMenuGroup,
  OverflowMenuItem,
  Title,
  EmptyState,
  EmptyStateVariant,
  EmptyStateIcon,
  EmptyStateBody,
  Bullseye,
  Spinner,
} from '@patternfly/react-core';
import { fetchCreatePortalLink, GetOrganizationsError, GetOrganizationsResponse } from 'src/query/orgs';
import { ExclamationCircleIcon, EditAltIcon, SearchIcon, UsersAltIcon } from '@patternfly/react-icons';
import { useKeycloak } from '@react-keycloak/web';
import { OrganizationRepresentation, useGetOrganizations } from 'src/query/orgs';
import { EditOrganization } from './edit-organization';
import { baseUrl } from 'src';
import { Link } from 'react-router-dom';
import { generateBasePath } from '@app/routes';
import { API } from 'src/api';

type RoleCheck = {
  orgId: OrganizationRepresentation['id'];
  hasManageOrgRole: boolean;
  hasViewMembersRole: boolean;
};

const columnNames = {
  name: 'Name',
  id: 'ID',
  displayName: 'Display Name',
};

export const OrganizationsTable: React.FC = () => {
  const { keycloak } = useKeycloak();
  const {
    data: organizations = [],
    error,
    isLoading,
    refetch,
  } = useGetOrganizations({
    pathParams: { realm: 'self' },
    headers: {
      Authorization: `Bearer ${keycloak.token}`,
    },
  });
  const [editOrganization, setEditOrganization] = useState<OrganizationRepresentation | null>(null);
  const [roleChecks, setRoleChecks] = useState<RoleCheck[]>([]);

  const fetchRoleCheck = async ({ userId, orgId, roleName }) => {
    try {
      const roleCheck = await API.Orgs.checkMemberRoleInOrg({ roleName, orgId, userId });

      if (roleCheck.ok) {
        return true;
      }
    } catch (e) {
      console.log('[Error] Role check for user failed.');
      return false;
    }
    return false;
  };

  const getRolePermissions = async () => {
    const user = await keycloak.loadUserProfile();

    const rolePromise: RoleCheck[] = await Promise.all(
      organizations.map(async (org) => ({
        orgId: org.id,
        hasManageOrgRole: await fetchRoleCheck({ userId: user.id, orgId: org.id, roleName: 'manage-organization' }),
        hasViewMembersRole: await fetchRoleCheck({ userId: user.id, orgId: org.id, roleName: 'view-members' }),
      }))
    );
    setRoleChecks(rolePromise);
  };

  const checkRole = (roleName: 'hasManageOrgRole' | 'hasViewMembersRole', orgId: OrganizationRepresentation['id']) => {
    const org = roleChecks.find((rc) => rc.orgId === orgId);
    return org && org[roleName];
  };

  useEffect(() => {
    getRolePermissions();
  }, [organizations]);

  const getPortalLink = (orgId, userId) => {
    const linkPromise = fetchCreatePortalLink({
      pathParams: {
        realm: 'self',
        orgId,
      },
      variables: {
        userId: userId,
      },
      headers: {
        Authorization: `Bearer ${keycloak.token}`,
        'Content-Type': 'application/x-www-form-urlencoded',
      },
    });
    toast.promise(linkPromise, {
      loading: 'Generating SSO setup link. Please standby.',
      success: () => {
        linkPromise.then((l) => window.open(l.link));
        return 'Link generated successfully.';
      },
      error: 'Error generating link. Please try again.',
    });
  };

  return (
    <>
      <TableComposable aria-label="Actions table">
        <Thead>
          <Tr>
            <Th>{columnNames.id}</Th>
            <Th>{columnNames.name}</Th>
            <Th>{columnNames.displayName}</Th>
            <Td></Td>
          </Tr>
        </Thead>
        <Tbody>
          {isLoading && (
            <Tr>
              <Td colSpan={5}>
                <Bullseye>
                  <EmptyState>
                    <EmptyStateIcon variant="container" component={Spinner} />
                    <Title size="lg" headingLevel="h2">
                      Loading
                    </Title>
                  </EmptyState>
                </Bullseye>
              </Td>
            </Tr>
          )}
          {error && !isLoading && (
            <Tr>
              <Td colSpan={5}>
                <EmptyState variant={EmptyStateVariant.small}>
                  <EmptyStateIcon icon={ExclamationCircleIcon} />
                  <Title headingLevel="h2" size="lg">
                    No Organizations found
                  </Title>
                  <EmptyStateBody>
                    There was an error retrieving data. Check your connection and reload the page.
                  </EmptyStateBody>
                </EmptyState>
              </Td>
            </Tr>
          )}
          {organizations.length === 0 && !isLoading && !error && (
            <Tr>
              <Td colSpan={5}>
                <EmptyState>
                  <EmptyStateIcon icon={SearchIcon} />
                  <Title headingLevel="h2" size="lg">
                    No Organizations found
                  </Title>
                  <EmptyStateBody>No Organizations are available.</EmptyStateBody>
                </EmptyState>
              </Td>
            </Tr>
          )}
          {organizations.length > 0 &&
            !isLoading &&
            !error &&
            organizations.map((org) => {
              //const canSsoSetup = checkRole('view-organization', org.id) && checkRole('manage-organization', org.id) && checkRole('view-identity-providers', org.id) && checkRole('manage-identity-providers', org.id);
              const canSsoSetup = true;
              return (
              <Tr key={org.name}>
                <Td dataLabel={columnNames.id}>{org.id}</Td>
                <Td dataLabel={columnNames.name}>{org.name}</Td>
                <Td dataLabel={columnNames.displayName}>{org.displayName}</Td>
                <Td isActionCell>
                  <OverflowMenu breakpoint="md">
                    <OverflowMenuContent>
                      <OverflowMenuGroup groupType="button">
                        <OverflowMenuItem>
                          <Button
                            variant="link"
                            icon={<EditAltIcon />}
                            onClick={() => setEditOrganization(org)}
                            isDisabled={!checkRole('hasManageOrgRole', org.id)}
                          >
                            Edit
                          </Button>
                        </OverflowMenuItem>
                        <OverflowMenuItem>
                          <Link to={generateBasePath(`/organizations/${org.id}/members`)}>
                            <Button
                              variant="link"
                              icon={<UsersAltIcon />}
                              isDisabled={!checkRole('hasViewMembersRole', org.id)}
                            >
                              Members
                            </Button>
                          </Link>
                        </OverflowMenuItem>
                        <OverflowMenuItem>
                          <Button
                            variant="link"
                            isDisabled={!canSsoSetup}
                            icon={<ExternalLinkSquareAltIcon />}
                            onClick={() => getPortalLink(org.id, keycloak.tokenParsed?.sub)}
                          >
                            SSO Setup
                          </Button>
                        </OverflowMenuItem>
                     </OverflowMenuGroup>
                    </OverflowMenuContent>
                  </OverflowMenu>
                </Td>
              </Tr>
	    );
          })}
        </Tbody>
      </TableComposable>
      {editOrganization && (
        <EditOrganization org={editOrganization} refresh={() => refetch()} clearOrg={() => setEditOrganization(null)} />
      )}
    </>
  );
};
